$color-red: #ED3E3F;
$color-gray: #D9D9D9;
$color-black: #292929;
$color-blue: #2B3262;
$color-bg: #010012;


@font-face {
    font-family: 'Montserrat', Sans-serif;
    font-display: swap;
    src: url('../fonts/Montserrat-Regular.ttf');
    font-weight: normal;
    font-style: normal; }

@font-face {
    font-family: 'Montserrat', Sans-serif;
    font-display: swap;
    src: url('../fonts/Montserrat-SemiBold.ttf');
    font-weight: bold;
    font-style: normal; }

@font-face {
    font-family: 'Rubik', Sans-serif;
    font-display: swap;
    src: url('../fonts/Rubik-Regular.ttf');
    font-weight: normal;
    font-style: normal; }

html {
    scroll-behavior: smooth;
    font-family: 'Montserrat';
    font-weight: normal;
    line-height: 1.4; }

h1, h2, h3, h4, h5,h6, span {
    line-height: 1; }

img {
    max-width: 100%;
    height: auto; }

/* Первая секция */

.container {
    max-width: 1140px;
    margin: 0 auto;
    padding: 0 10px; }

.main-section {
    position: relative;
    height: 840px;
    background: url(../img/main-section-bg.jpeg) center center/cover no-repeat;
    padding-top: 25px;
    overflow: hidden;
    &__header {
        img {
            width: 90%;
            height: auto; } }
    &__nav-container {
        display: flex;
        justify-content: space-between;
        align-items: center; }
    &__content-container {
        display: flex;
        height: 100%;
        align-items: center; } }

/* Навигация */
.nav-menu {
    &__list {
        display: flex; }
    &__list-item {
        margin-right: 35px;
        font-weight: bold;
        font-size: 14px;
        a {
            text-decoration: none;
            color: #fff;
            position: relative;
            cursor: pointer;
            &:after {
                content: "";
                display: block;
                position: absolute;
                right: 0;
                bottom: -5px;
                width: 0;
                height: 3px;
                background-color: $color-red;
                transition: width 0.5s; }
            &:hover:after {
                content: "";
                width: 100%;
                display: block;
                position: absolute;
                left: 0;
                bottom: -5px;
                height: 3px;
                background-color: $color-red;
                transition: width 0.5s; } } } }

.btn-phone {
    &__list-item {
        margin-right: 25px;
        font-weight: bold;
        font-size: 18px;
        a {
            text-decoration: none;
            color: #fff; }
        a:hover {
            color: $color-red;
            transition: .3s linear; } } }

/* Содержимое главного экрана */

.left-wrapper {
    width: 50%;
    margin-top: -80px;
    &__title {
        margin-bottom: 12px; }
    &__description {
        margin-bottom: 50px; }
    &__text-title {
        margin-bottom: 15px; }
    &__list-adress {
        margin-bottom: 40px;
        img {
            width: 20px;
            height: 20px;
            margin-bottom: -5px; } } }

.title {
    font-weight: bold;
    font-size: 49px;
    line-height: 1;
    color: #fff; }

.description {
    font-size: 34px;
    color: #fff;
    span {
        color: $color-red;
        text-decoration: underline; } }

.text-title {
    color: #fff;
    font-weight: 500;
    font-size: 22px; }

.list-adress {
    color: #fff;
    font-size: 16px;
    &__main-list:not(:last-child) {
        padding-bottom: 10px; } }

.btn-main {
    font-size: 16px;
    color: #fff;
    background: linear-gradient(to left, #7300CE, #7300CE, #EE164D, #EE164D);
    text-decoration: none;
    padding: 20px 30px;
    border-radius: 90px;
    background-size: 200%;
    transition: .5s;
    border: solid 2px #ffffff;
    cursor: pointer;
    &:hover {
        background-position: right;
        color: #fff; } }

.right-wrapper {
    img {
        position: absolute;
        bottom: 0;
        left: 51%;
        width: 500px;
        height: auto; } }

.second-section {
    max-width: 1140px;
    margin-top: -80px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    z-index: 2;
    &__container {
        display: flex;
        background: url(../img/blok2_1bg.png) center center/cover no-repeat; }
    &__column {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        height: 280px;
        background: url(../img/blok2_bg1.png) center center/cover no-repeat;
        text-align: center;
        justify-content: center;
        align-content: center; }
    &__title {
        width: 100%;
        font-size: 14px;
        line-height: 1.3;
        color: #fff;
        span {
            color: $color-red; } } }

/* Третья секция */

.third-section {
