            background: $color-black; }
        &_blue {
            background: $color-blue; }
        &_red {
            background: $color-red; } }
    &__title {
        font-size: 14px;
        line-height: 1.2;
        font-weight: bold;
        color: $color-black;
        margin-bottom: 22px;
        &_white {
            color: #fff; } }
    &__text {
        font-size: 14px;
        font-weight: normal;
        color: $color-black;
        &_white {
            color: #fff; } } }

.fourth-section {
    background: $color-bg url(../img/fourth-section-bg.jpg)center center/cover no-repeat;
    padding-bottom: 100px;
    &__text {
        text-align: center;
        padding-bottom: 50px;
        font-weight: bold; }
    &__tab {
