            display: inline-block;
            width: 100%;
            color: #fff;
            font-size: 16px;
            line-height: 1.2; }
        a {
            color: #fff;
            font-size: 26px;
            text-decoration: underline; }
        a:hover {
            color: $color-red;
            transition: .3s linear; } } }

