    &__title-list {
        color: #fff;
        font-weight: bold;
        font-size: 18px;
        line-height: 1;
        margin-bottom: 15px; }
    &__text {
        color: #fff;
        font-weight: bold;
        font-size: 20px;
        line-height: 1.2; }
    &__list-box {
        color: #fff;
        font-size: 16px;
        line-height: 1.2;
