/* Пятая секция */

.fifth-section {
    background: url(../img/fifth_section-bg.jpg) center/cover no-repeat;
    min-height: 920px;
    &__container {
        display: flex;
        padding: 30px 0 50px 0; }
    &__column-l {
        position: relative;
        width: 50%;
        display: flex;
        flex-wrap: wrap;
        align-content: center; }
    &__column-r {
        position: relative;
        width: 50%;
        display: flex;
        flex-wrap: wrap;
        align-content: center;
