/* Шестая секция */
.carpark {
    background: $color-bg url(../img/carpark-bg.jpg) top center/contain no-repeat;
    height: 820px;
    text-align: center;
    padding-top: 180px;
    &__container {}
    &__decor {
        width: 100px;
        margin: 0 0 30px -40px;
        /* косяк фоновой картинки */ }
    &__text {
        font-size: 18px;
        margin: 0 0 60px -40px;
        span {
            color: #fff;
            font-weight: bold; } }
    &__title {
        color: $color-red;
        text-decoration: underline;
        margin: 0 0 5px -30px; }
    &__description {
        font-weight: bold;
        font-size: 36px;
        margin: 0 0 50px -30px; }
    &__column {
        display: flex;
        flex-wrap: nowrap;
