/* Седьмая секция */
.document {
    background-color: $color-bg;
    margin: 0 auto;
    background: url(../img/document-bg.jpg) center center/cover no-repeat;
    padding-bottom: 120px;
    &__container {
        display: flex;
        flex-wrap: wrap; }
    &__column {
        width: 50%;
        align-items: center;
        display: flex;
        flex-wrap: wrap;
        align-content: center;
        justify-content: flex-start;
        &:last-child {
            justify-content: center; } }
    &__title {
        font-size: 40px;
        margin-bottom: 54px;
        line-height: 1.2;
        span {
            color: $color-red; } }
    &__podium {
        margin-top: -60px;
        width: 86%; } }

/* Slick Slider */

.slick-item {
    .slick-list {
        width: 320px;
        height: auto; }
    &__img {
        border: solid 2px;
        border-color: #2b32625e; }
    &__icons-l {
        cursor: pointer;
        margin-right: 12px; }
    &__icons-r {
        cursor: pointer;
        margin-left: 12px; }
    &__btn {
        margin: 0 5px; } }

.slick-btn {
    background: none;
    border: none;
    position: relative;
    z-index: 5; }

/* Восьмая секция */
.reviews {
    background: $color-bg;
    height: 550px;
    position: relative;
    overflow-x: hidden;
    &__container {
        text-align: center;
        position: relative; }

    &__title {
        font-size: 42px;
        margin-bottom: 40px;
        span {
            color: $color-red;
            text-decoration: underline; } }
    &__slider {
        display: flex;
        justify-content: center;
        .slick-list {
            width: auto;
            height: auto; } }
    &__decor-l {
        position: absolute;
        left: 0;
        top: 100px;
        height: 400px;
        width: 300px;
        background: linear-gradient(to right, #010012 15%, rgba(1, 0, 18, 0.5), rgba(1, 0, 18, 0.01)); }
    &__decor-r {
        position: absolute;
        right: 0;
        top: 100px;
        height: 400px;
        width: 300px;
        background: linear-gradient(to left, #010012 15%, rgba(1, 0, 18, 0.5), rgba(1, 0, 18, 0.01)); }
    &__button {
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-around;
        margin: -215px -70px 0 -70px; } }

.slick-initialized .slick-slide {
    display: flex; }

.slider {
    &__item {
        justify-content: center;
        align-items: center;
        height: 350px;
        align-content: center;
        flex-wrap: wrap;
        padding: 100px 240px;
        margin: 0 20px;
        background: url(../img/reviews__bg.jpg) center center/contain no-repeat; }
    &__title {
        font-weight: 700;
        font-size: 14px;
        line-height: 1.2;
        color: $color-blue;
        margin-bottom: 20px; }

    &__text {
        font-weight: 500;
        font-size: 14px;
        line-height: 1.2;
        margin-bottom: 20px;
        color: $color-blue; } }

/* Девятая секция */
.faq {
    background: $color-bg url(../img/faq-bg2.jpg) top center/cover no-repeat;
    text-align: center;
    padding: 70px 0;
    &__title {
        font-size: 42px;
        margin-bottom: 40px;
        span {
            color: $color-red; } } }

/* accordion */
.card {
    background-color: #ffffff00;
    &-body {
        font-size: 18px;
        color: #fff;
        font-weight: 700;
        line-height: 1.2;
        padding: 30px 30px 46px 30px; } }

.card-header {
    background-color: #D9D9D9; }

.accordion>.card>.card-header {
    border-radius: 90px;
    margin-bottom: 16px; }

.btn:not(:disabled):not(.disabled) {
    cursor: pointer;
    font-weight: 700;
    font-size: 18px;
    line-height: 40px;

    color: #2B3262; }

.mb-0 {
    display: flex;
    justify-content: space-between;
    .btn:focus {
        box-shadow: none; } }

/* Десятая секция */

.feedback {
    background: $color-bg;
    color: #fff;
    overflow: hidden;
    &__container {
        display: flex;
        flex-wrap: nowrap;
        padding-top: 220px;
        position: relative; }
    &__column {
        position: relative;
        width: 50%;
        z-index: 2; }
    &__key {
        position: absolute;
        top: -40%;
        left: -20%;
        width: 50%; }
    &__circle {
        position: absolute;

        top: -40%;
        right: -23%;
        width: 75%; }
    &__boy {
        position: absolute;
        bottom: 0;
        left: -13%;
