    &__item {
        background: url(../img/form_footer.png) center center/contain no-repeat;
        padding: 50px 130px;
        width: 600px;
        text-align: center; }
    &__title-fb {
        margin-bottom: 28px; }
    &__text-fb {
        margin-bottom: 28px; }
    &__form {
        margin-bottom: 28px;
        position: relative;
        z-index: 99; }
    &__text {
        font-weight: 300;
        font-size: 12px;
        line-height: 16px; }
    &__car {
