.title-fb {
    font-weight: 700;
    font-size: 22px;
    line-height: 32px;
    text-transform: uppercase;
    span {
        color: $color-red;
        text-decoration: underline; } }
.text-fb {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px; }

.feedback {
    &__form {
        input, button {
            outline: none;
            border: 1px solid #DCDEE6;
            border-radius: 90px; }
        input {
            margin-bottom: 16px;
            width: 100%;
            height: 56px;
            background: #fff;
            padding-left: 25px;
            color: #202020;
            font-size: 14px;
            line-height: 24px;
            transition: .3s;
            &::-webkit-input-placeholder {
                color: #A3ACB2; }
            &:hover {
                border: 1px solid $color-red; }
