                border: 1px solid $color-red; }
            &:focus {
                border: 1px solid $color-red; } }
        button {
            width: 100%;
            font-size: 16px;
            color: #fff;
            background: linear-gradient(to left, #7300CE, #7300CE, #EE164D, #EE164D);
            text-decoration: none;
            padding: 20px 30px;
            border-radius: 90px;
            background-size: 200%;
            transition: .5s;
            border: solid 2px #ffffff;
            cursor: pointer;
            &:hover {
                background-position: right;
                color: #fff; } } } }

/* Footer */

.footer {
    background: $color-bg;
    padding: 60px 0 30px 0;
