.politica {
    margin-bottom: 24px;
    a {
        color: #efefef;
        font-size: 12px;
        line-height: 1.4;
        font-weight: 300;
        transition: .3s;
        text-decoration: underline;
        &:hover {
            color: $color-red; } } }
